// 
import { wx_config } from "@/api/index";

const wx = require("weixin-js-sdk");

const shareUrl = `${window.location.origin}/aid_sign`;

const jsApiList = [
  "updateAppMessageShareData",
  "updateTimelineShareData",
  "onMenuShareTimeline",
  "onMenuShareAppMessage",
  "getLocation",
  "chooseWXPay"
];

export function wxAuthority(options) {
  return new Promise((resolve, reject) => {
    wx.config({
      debug: false,
      appId: options.appid, // 
      timestamp: options.timestamp, // 
      nonceStr: options.noncestr, // 
      signature: options.signature, // 
      jsApiList // JS
    });

    wx.ready(() => {
      wx.checkJsApi({
        jsApiList,
        success(res) {
          console.log(res);
        }
      });
      resolve();
    });
    wx.error(err => {
      console.log(err.message);
      // Vue.$vux.toast.show({
      //   text: `${err}`,
      //   type: 'text'
      // })
      reject();
    });
  });
}

export function authority(url) {
  return new Promise(resolve => {
    wx_config({ url }).then(({ data }) => {
      sessionStorage.setItem("appid", data.appid);
      wxAuthority(data).then(() => {
        resolve();
      });
    });
  });
}

export function setWxShare(options) {
  const link = `${shareUrl}?shareid=${options.shareId}`;
  console.log("", link);
  const shareOption = {
    title: options.title, // 
    desc: options.desc, // 
    link, // JS
    imgUrl: options.imgUrl // 
  };
  console.log("", shareOption);
  wx.updateAppMessageShareData({
    ...shareOption,
    success() {
      console.log("");
    }
  });
  wx.updateTimelineShareData({
    ...shareOption,
    success() {
      console.log("");
    }
  });
}

export function startWXPay(options) {
  return new Promise(resolve => {
    wx.chooseWXPay({
      timestamp: options.timeStamp, // jssdktimestamptimeStampS
      nonceStr: options.nonceStr, //  32 
      package: options.package, // prepay_idprepay_id=\*\*\*
      signType: options.signType, // 'SHA1''MD5'
      paySign: options.paySign, // 
      success(res) {
        resolve(res);
      }
    });
  });
}



