import http from '@/plugins/axios'

export function wx_config(params) {
    /**
     * @description 
     * @retrun (promise)
     * 
     * @params (object):
     *  @url (string): URL#
     */
    return http({
        url: 'v1/wx/config',
        method: 'get',
        params
    })
}

export function login (params) {
    /**
     * @description 
     * @return (promise)
     * 
     * @params (object):
     *  @code (string): code
     */

    return http({
        url: 'v1/customers/login',
        method: 'post',
        params
    })
}


