/**
 * @description 
 * @author <beyondc@foxmail.com>
 */
import WCard from '@/components/WCard'
import WMerchantItem from '@/components/WMerchantItem'
// 
import Qs from 'qs'
import baseUrl from '@/utils/doman'
export default {
    name: 'MerchantRecommend',
    components: {
        WCard,
        WMerchantItem
    },
    props: {
        list: {
            type: Array,
            required: true,
        }
    },
    computed: {
        storeList () {
            return this.list.map(item => {
                item.desc = `${item.checkin_days}${item.checkin_num}`
                const file ={
                    filename: item.store_avatar,
                    type: 'avatar'
                }
                item.store_avatar = `${baseUrl}v1/files/download?${Qs.stringify(file)}`
                return item
            })
        }
    },
    data() {
        return {}
    },
    methods: {
        showAll() {
            /**
             * @description 
             *  - 
             * @return (void)
             */
            this.$router.push({ name: 'store_list' })
        }
    }
}



